.bottomTab {
  position: fixed;
  bottom: 0px;
  width: 100%;
  background-color: #fff;
  height: 48px;
  display: -ms-flexbox;
  display: flex;
  border-top: 1px solid #e2e2e2; }
  .bottomTab__icon {
    height: 22px;
    width: 22px; }
  .bottomTab__icon--movie {
    background: transparent url("../icons/movie.png") center/cover no-repeat; }
  .bottomTab__icon--user {
    background: transparent url("../icons/user.png") center/cover no-repeat; }
  .bottomTab__text {
    display: inline-block;
    margin-top: 6px;
    font-size: 10px;
    color: #666666; }
  .bottomTab__item {
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    text-decoration: none; }
  .bottomTab__item--active .bottomTab__icon--movie {
    background: transparent url("../icons/movie-solid.png") center/cover no-repeat; }
  .bottomTab__item--active .bottomTab__icon--user {
    background: transparent url("../icons/user-solid.png") center/cover no-repeat; }
  .bottomTab__item--active .bottomTab__text {
    color: #ff4c64; }



